/* src/App.css */
.container {
  max-width: 1200px;
  margin: 20px auto;
  padding: 0 20px;
}

.calculator-card,
.rates-card {
  margin-bottom: 20px;
  padding: 20px;
}

.form-group {
  margin-bottom: 20px;
}

.price-display {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0;
  padding: 10px;
  border-top: 1px solid #eee;
}

.all-prices {
  margin-top: 20px;
}

.price-card {
  margin-bottom: 10px;
  padding: 15px;
}

.price-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
  margin-top: 10px;
}

.price-block {
  padding: 10px;
  border-radius: 4px;
}

.price-block.green {
  background-color: #f0f9f0;
}

.price-block.ripe {
  background-color: #fdfbf0;
}

.add-location {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

.location-card {
  margin-bottom: 10px;
  padding: 15px;
}

.location-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.rate-section {
  padding: 15px;
  margin: 10px 0;
  border-radius: 4px;
}

.rate-section.green {
  background-color: #f0f9f0;
}

.rate-section.ripe {
  background-color: #fdfbf0;
}